}

.breadcrumb-div {
  font-size: 0.8rem;
}

.breadcrumb-text {
  display: inline-block;
  text-decoration: underline;
}

.breadcrumb-text::first-letter,
.breadcrumb-last-section::first-letter {
  text-transform: uppercase;
}

.breadcrumb-separator {
  margin: 0 5px;
}

.breadcrumb-last-section {
