  display: grid;
  grid-template-columns: repeat(2, 50%);
  grid-gap: 100px 70px;
}

.prod-sum-card-link {
  display: grid;
  grid-template-columns: 40% 60%;
  height: 250px;
  font-size: 0.85rem;
  color: black;
}

.prod-sum-img-div,
